<template>
    <div>
        <Head :name="title" />
        <p></p>
        <van-collapse v-model="activeNames">
            <van-collapse-item title="" name="2">
                
            </van-collapse-item>
            <van-collapse-item title="" name="3">
                
            </van-collapse-item>

        </van-collapse>
    </div>
</template>
<script>
import Head from '@/components/Head';
import { Collapse, CollapseItem } from 'vant';
export default {
    name:'About',
    components:{
        Head
    },
    data(){
        return{
            title:'',
            activeNames:['1']
        }
    }
}
</script>
<style scoped>
    p{
        margin: 66px 0 20px 20px;
        font-weight: 900;
        font-size: 20px;
    }
</style>




