import Mock from 'mockjs';
var Random=Mock.Random;
Random.extend({
	xingzuo: function(date) {
        var constellations = ['', '', '', '', '', '', '', '', '', '', '', '']
        return this.pick(constellations)
    }
})
var product=Mock.mock({
	"product|3-5":[
		{
			"email":'@email',
			"xz":"@xingzuo"
		}
	]
})

Mock.mock('http://www.jingdong.com/api/product',product)


