<template>
    <div class='container'>
        <Head :name='title' />
        <div class='top'></div>
        <div class='nav'>
            <h3></h3>
            <p></p>
        </div>
        <div class='list'>
            <h3></h3>
             <span v-for='item in 6'>
                <p></p>
                 <b>
                    <i></i>
                    <em></em>
                </b>
            </span>
        </div>
        <div class='bot'>
            <h3></h3>
            <p>1.</p>
            <p>2.</p>
            <p>3.</p>
        </div>
    </div>
</template>
<script>
import Head from '@/components/Head'

export default {
    name:'Club',
    components:{
        Head
    },
   data(){
       return{
         arr:[1,2,3,4],
         title:''  
       }
   }
}
</script>
<style scoped>

.top{
    height:150px;
    background: #cecece;
}
.nav{
    height:80px;
    padding:20px ;
}
h3{
    font-size:20px;
    height:22px;
    line-height:22px;
}
.nav p{
    font-size:16px;
    height:45px;
    line-height:20px;
}
.list{
    display: flex;
    flex-direction: column;
    padding: 0 20px;
}
.list span{
    display: flex;
    height:60px;
    margin:10px 0;
}
.list span p{
        width:51px;
        height:51px;
        border:1px solid #cccccc;
        border-radius: 50%;
    }
.list span b{
        width:300px;
        height:42px;
        display: flex;
        flex-direction: column;
        margin-left:10px;
        line-height:21px;
        margin-top:15px;
    }
    .list span i{
        font-size:16px;
        color:#000;
        font-style:normal;
    }
    .list span em{
        font-size:14px;
        color:#cccccc;
        font-style:normal;
    }
    .bot{
        height:100px;
    padding:20px ;

     }
     .bot h3{
         font-size:16px;
     }
     .bot p{
          font-size:14px;
          color:#cccccc;
     }
</style>


