        <Head :name='title'/>
        <div class="box">
            <van-card v-for="item in list"
                thumb="https://img.yzcdn.cn/upload_files/2017/07/02/af5b9f44deaeb68000d7e4a711160c53.jpg"
                :title=item.uname
                :desc=item.location
                />
                <van-tag></van-tag> 
            <p>""</p>
            <ul>
                <li>
                    <b>/</b>
                    <i>100000</i>
                </li>
                <em></em>
                <li>
                    <b></b>
                    <i>4522</i>
                </li>
                <em></em>
                <li>
                    <b></b>
                    <i>576</i>
                </li>
            </ul>
            <van-circle class="svg"
                v-model="currentRate"
                :rate="45"
                :speed="100"
                :text="text"
            />
            <span><a></a>8</span>
        </div>
        <div class="p"></div>
        <div class='part'>
            <span>
                <a></a>
                <b><van-icon name="underway-o" />
                    1</b>
            </span>
            <p>
                ""17
,65,,2014
,,:;:;:;:
20181218,,17
,,,

,,1218,
,,,,
,,,
,,
,,,,
,,,

            </p>
            <ul>
                <li v-for='item in 3'>
                    <van-icon name="video-o"/>
                </li>
            </ul>
        </div>
        <div class="p"></div>
        <div class="prove">
            <a></a>
            <div class='need'>
                <p v-for="item in 3">
                    <a></a>
                    <b>
                        <span>
                            <i>*</i>
                            <i></i>
                            <i></i>
                        </span>
                    </b>
                </p> 
                <em>/</em> 
            </div> 
            <div class='bot'>
                <p>
                    <a>12</a>
                    <a></a>
                </p>
                <ul>
                    <li v-for="item in 6"></li>
                </ul>
            </div>
        </div>
        <div class="p"></div>   
        <div class='last'>
            <a></a>
            <ul>
                <li v-for="item in 3">
                    <van-card 
                        thumb="https://img.yzcdn.cn/upload_files/2017/07/02/af5b9f44deaeb68000d7e4a711160c53.jpg"
                        title=""
                        desc=""
                        />
                        <p>1</p> 
                </li>
            </ul>
        </div>
        <van-tabbar class="foot">
            <van-icon name="like-o" />
            <van-icon name="comment-circle-o" />
            <van-icon name="replay" />
            <van-button plain type="default"  to="/contribute"></van-button>
       </van-tabbar>
    </div>
</template>
<script>
import axios from 'axios'
import Head from '@/components/Head'
export default {
    name:'Detail',
    components:{
        Head
    },
     data(){
        return{
            active:'',
            list:[],
            currentRate: 0,
            title:''
        }
    },
     mounted(){
        var _this=this;
        axios({
            method:'post',
            url:'http://47.107.138.115:8081/weishan/weishan.weishan/project/list.do',
            params:{
                limit:1,
                page:1,
            }
        })
        .then(function(data){
            _this.list=data.data.data.records
            console.log(_this.list);
        })
        .catch(function(error){
            console.log(error)
        })
    },
    computed: {
    text() {
      return this.currentRate.toFixed(0) + '%'
    }
  }
}
</script>
<style scoped>
em,i{
    font-style: normal;
}
*{
    margin:0;
    padding:0;
}
.foot{display: flex;justify-content: space-between;align-items: center;background: #eee}
.box{
    width:90%;
    margin:46px auto;
    height:270px;
}
.van-card__header{
    width: 80%;
    float: left;
}
.van-card__thumb{
width:42px;
height:42px;
} 
.van-card__content{
height:42px;
}
.van-card__img{
border-radius:50%;
}
.box p{
font-size:14px;
margin-top:30px;
padding-left:20px;
