}
.box ul{
height:50px;
display:flex;
justify-content: space-around;
margin-top:20px;
} 
.box ul em{
width:1px;
height:30px;
background:#cccccc;
}
.box ul li{
display:flex;
font-size:12px;
flex-direction: column;
color:#cccccc;
} 
.svg{
margin:30px 0 0 130px;
}
.box span{
display: flex;
justify-content: center;
color:#cccccc;
font-size:12px;
}
.box span a{
display: block;
width:10px;
height:10px;
border:1px solid #cecece;
margin:3px 3px 0 0;
} 
.p{
width:100%;
height:6px;
background:#bbbbbb;
}
.part{
height:430px;
width:90%;
margin:0 auto;
}
.part span{
height:30px;
line-height: 40px;
display: flex;
justify-content: space-between;
font-size:14px;
}
.part span b{
    font-size:12px;
    color:#bbbbbb;
}
.part p{
font-size:12px;
color:#cccccc;
}
.part ul{
    height:95px;
    display: flex;
    justify-content:space-around;
    margin-top:10px;
}
.part ul li{
width:29px;
height:15px;
background:#cccccc;
