height:67px;
border-bottom: 1px solid #cecece;
justify-content: space-around;
padding:10px 0;
}
.need p b{
display: flex;
flex-direction: column;
}
.need p b span{
display: flex;
flex-direction: column;
}
.need em{
text-align: center;
display: block;
margin-top:15px;
}
.bot p{
    height:30px;
    display: flex;
    justify-content: space-between; 
    font-size:12px;
    color:#bbbbbb;
} 
.bot ul{
    display: flex;
    justify-content: space-around;
}
.bot ul li{
width:39px;
height:39px;
border:1px solid #cceecc;
border-radius: 50%;
}
.last{
    width:90%;
    margin:0 auto;
    height:200px;
}
.last a{
    font-size:12px;
    color:#bbbbbb;
    height:30px;
    line-height: 30px;
}
.last ul li{
    border-bottom: 1px solid #bbbbbb;
    height: 50px;
    padding-top:10px;
}
.last ul li p{
    font-size:12px;
    color:#cccccc;
}
</style>


